import React, { useState, useCallback, useRef, useEffect } from 'react';
import { askQuestion } from '../services/api';
import { AnswerReq } from '../types';
import './CodeSearch.css';

interface CodeSearchProps {
  repoId: string;
  repoName: string;
}

const CodeSearch: React.FC<CodeSearchProps> = ({ repoId, repoName }) => {
  const [question, setQuestion] = useState('');
  const [answer, setAnswer] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [isStreaming, setIsStreaming] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [theme, setTheme] = useState<'light' | 'dark'>('light');
  const isMountedRef = useRef(true);
  const abortControllerRef = useRef<AbortController | null>(null);

  // 
  const toggleTheme = useCallback(() => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
    document.documentElement.setAttribute('data-theme', newTheme);
    localStorage.setItem('theme', newTheme);
  }, [theme]);

  // 
  useEffect(() => {
    const savedTheme = localStorage.getItem('theme') as 'light' | 'dark' | null;
    const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
    const initialTheme = savedTheme || (prefersDark ? 'dark' : 'light');
    
    setTheme(initialTheme);
    document.documentElement.setAttribute('data-theme', initialTheme);
  }, []);

  // 
  useEffect(() => {
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    const handleChange = (e: MediaQueryListEvent) => {
      if (!localStorage.getItem('theme')) {
        const newTheme = e.matches ? 'dark' : 'light';
        setTheme(newTheme);
        document.documentElement.setAttribute('data-theme', newTheme);
      }
    };

    mediaQuery.addEventListener('change', handleChange);
    return () => mediaQuery.removeEventListener('change', handleChange);
  }, []);

  useEffect(() => {
    // 
    isMountedRef.current = true;
    
    return () => {
      // 
      console.log('');
      isMountedRef.current = false;
      if (abortControllerRef.current) {
        abortControllerRef.current.abort();
      }
    };
  }, []);

  // answer
  useEffect(() => {
    console.log('answer:', { answer, answerLength: answer.length, isStreaming });
  }, [answer, isStreaming]);

  // 
  useEffect(() => {
    console.log('CodeSearch');
    return () => {
      console.log('CodeSearch');
    };
  }, []);

  const handleAsk = useCallback(async () => {
    if (!question.trim()) return;

    setIsLoading(true);
    setError(null);
    setAnswer('');
    setIsStreaming(false); // 
    console.log(':', question.trim());

    // 
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }

    // AbortController
    abortControllerRef.current = new AbortController();

    try {
      const response = await askQuestion(
        { id: repoId, question: question.trim() }, 
        abortControllerRef.current.signal,
        // 
        (chunk: string, isComplete: boolean) => {
          console.log('onChunk:', { chunk, isComplete, chunkLength: chunk?.length, isMounted: isMountedRef.current });
          
          // 
          if (!isMountedRef.current) {
            console.log('');
            return;
          }
          
          try {
            if (isComplete) {
              // 
              if (chunk && chunk.trim()) {
                // chunk
                setAnswer(chunk);
                console.log(':', chunk.length);
              } else {
                // 
                console.log(':', answer.length);
              }
              // 
              setIsStreaming(false);
              console.log('AI');
            } else if (chunk && chunk.trim()) {
              // 
              setAnswer(prev => {
                const newAnswer = prev + chunk;
                console.log('chunk:', chunk, ':', newAnswer.length, ':', newAnswer);
                return newAnswer;
              });
              // 
              setIsStreaming(true);
            } else {
              console.log('chunk:', chunk);
            }
          } catch (error) {
            console.error('chunk:', error);
          }
        }
      );
      
      if (isMountedRef.current) {
        // 
        console.log('response:', response);
      }
    } catch (err: any) {
      if (isMountedRef.current && err.name !== 'AbortError') {
        setError(err.message || '');
      }
    } finally {
      if (isMountedRef.current) {
        setIsLoading(false);
        // 
        setIsStreaming(false);
      }
    }
  }, [question, repoId, answer.length]);

  const handleKeyPress = useCallback((e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleAsk();
    }
  }, [handleAsk]);

  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    setQuestion(e.target.value);
  }, []);

  const handleButtonClick = useCallback(() => {
    handleAsk();
  }, [handleAsk]);

  const handleClear = useCallback(() => {
    setQuestion('');
    setAnswer('');
    setError(null);
    setIsStreaming(false);
    if (abortControllerRef.current) {
      abortControllerRef.current.abort();
    }
  }, []);

  // 
  const copyToClipboard = useCallback(async (text: string) => {
    try {
      await navigator.clipboard.writeText(text);
      // 
      console.log('');
    } catch (err) {
      console.error(':', err);
      // 
      const textArea = document.createElement('textarea');
      textArea.value = text;
      document.body.appendChild(textArea);
      textArea.select();
      try {
        document.execCommand('copy');
        console.log('');
      } catch (fallbackErr) {
        console.error(':', fallbackErr);
      }
      textArea.remove();
    }
  }, []);

  const handleErrorClose = useCallback(() => {
    setError(null);
  }, []);

  const formatAnswer = useCallback((text: string) => {
    if (!text) return null;
    
    // 
    const paragraphs = text.split('\n').filter(line => line.trim());
    
    return paragraphs.map((paragraph, index) => {
      // 
      if (paragraph.startsWith('<think>') && paragraph.endsWith('</think>')) {
        const thinkingContent = paragraph.slice(7, -8);
        return (
          <div key={index} className="thinking-container">
            <div className="thinking-header">
              
            </div>
            <div className="thinking-content">
              <div className="thinking-text">
                {formatAnswer(thinkingContent)}
              </div>
            </div>
          </div>
        );
      }
      
      // 
      if (paragraph.startsWith('```') && paragraph.endsWith('```')) {
        const code = paragraph.slice(3, -3);
        return (
          <pre key={index} className="code-block">
            <code>{code}</code>
          </pre>
        );
      }
      
      // 
      if (paragraph.includes('`')) {
        const parts = paragraph.split('`');
        return (
          <p key={index}>
            {parts.map((part, partIndex) => 
              partIndex % 2 === 0 ? part : <code key={partIndex}>{part}</code>
            )}
          </p>
        );
      }
      
      // 
      if (paragraph.startsWith('#')) {
        const level = paragraph.match(/^#+/)?.[0].length || 1;
        const title = paragraph.replace(/^#+\s*/, '');
        const HeadingTag = `h${Math.min(level, 6)}` as keyof JSX.IntrinsicElements;
        return <HeadingTag key={index}>{title}</HeadingTag>;
      }
      
      // 
      if (paragraph.match(/^[\s]*[-*+]\s/)) {
        const content = paragraph.replace(/^[\s]*[-*+]\s/, '');
        return <li key={index}>{content}</li>;
      }
      
      if (paragraph.match(/^[\s]*\d+\.\s/)) {
        const content = paragraph.replace(/^[\s]*\d+\.\s/, '');
        return <li key={index}>{content}</li>;
      }
      
      // 
      if (paragraph.startsWith('>')) {
        const content = paragraph.replace(/^>\s*/, '');
        return (
          <blockquote key={index}>
            {content}
          </blockquote>
        );
      }
      
      // 
      if (paragraph.match(/^[-*_]{3,}$/)) {
        return <hr key={index} />;
      }
      
      // 
      if (paragraph.includes('**') || paragraph.includes('__')) {
        const parts = paragraph.split(/(\*\*.*?\*\*|__.*?__)/);
        return (
          <p key={index}>
            {parts.map((part, partIndex) => {
              if (part.match(/^\*\*.*?\*\*$/) || part.match(/^__.*?__$/)) {
                const text = part.replace(/^\*\*|\*\*$/g, '').replace(/^__|__$/g, '');
                return <strong key={partIndex}>{text}</strong>;
              }
              return part;
            })}
          </p>
        );
      }
      
      // 
      if (paragraph.includes('*') || paragraph.includes('_')) {
        const parts = paragraph.split(/(\*.*?\*|_.*?_)/);
        return (
          <p key={index}>
            {parts.map((part, partIndex) => {
              if (part.match(/^\*.*?\*$/) || part.match(/^_.*?_$/)) {
                const text = part.replace(/^\*|\*$/g, '').replace(/^_|_$/g, '');
                return <em key={partIndex}>{text}</em>;
              }
              return part;
            })}
          </p>
        );
      }
      
      // 
      if (paragraph.includes('[') && paragraph.includes('](') && paragraph.includes(')')) {
        const linkRegex = /\[([^\]]+)\]\(([^)]+)\)/g;
        let lastIndex = 0;
        const elements = [];
        let match;
        
        while ((match = linkRegex.exec(paragraph)) !== null) {
          const [fullMatch, text, url] = match;
          elements.push(paragraph.slice(lastIndex, match.index));
          elements.push(
            <a key={`link-${match.index}`} href={url} target="_blank" rel="noopener noreferrer">
              {text}
            </a>
          );
          lastIndex = match.index + fullMatch.length;
        }
        
        if (lastIndex < paragraph.length) {
          elements.push(paragraph.slice(lastIndex));
        }
        
        return <p key={index}>{elements}</p>;
      }
      
      // 
      return <p key={index}>{paragraph}</p>;
    });
  }, []);

  const renderAnswer = useCallback((answer: string) => {
    return (
      <div className="answer-container">
        <h4>
          AI
          {isStreaming && (
            <span className="streaming-indicator">
              <span className="typing-dots">
                <span></span>
                <span></span>
                <span></span>
              </span>
            </span>
          )}
        </h4>
        <div className="answer-content">
          <div className={`answer-text ${isStreaming ? 'streaming' : ''}`}>
            {formatAnswer(answer)}
          </div>
          {isStreaming && (
            <div className="streaming-progress">
              <div className="progress-bar">
                <div className="progress-fill"></div>
              </div>
              <div className="progress-info">
                <span className="progress-text">AI...</span>
                <span className="char-count"> {answer.length} </span>
              </div>
            </div>
          )}
        </div>
      </div>
    );
  }, [formatAnswer, isStreaming, answer.length]);

  return (
    <>
      {/*  */}
      <button className="theme-toggle" onClick={toggleTheme} title={`${theme === 'light' ? '' : ''}`}>
        <span className="sun-icon"></span>
        <span className="moon-icon"></span>
      </button>

      <div className="code-search">
        <div className="search-header">
          <h3>AI</h3>
          <p>AI <strong>{repoName}</strong> </p>
        </div>

        <div className="search-input-container">
          <div className="search-input-wrapper">
            <input
              type="text"
              className="search-input"
              placeholder=""
              value={question}
              onChange={handleInputChange}
              onKeyPress={handleKeyPress}
              disabled={isLoading}
            />
            <button
              type="button"
              className="search-button"
              onClick={handleButtonClick}
              disabled={isLoading || !question.trim()}
            >
              {isLoading ? (
                isStreaming ? 'AI...' : '...'
              ) : ''}
            </button>
            <button
              type="button"
              className="clear-button"
              onClick={handleClear}
              title=""
            >
              
            </button>
          </div>
        </div>

        {error && (
          <div className="error-message">
            <span> {error}</span>
            <button
              type="button"
              className="error-close-button"
              onClick={handleErrorClose}
              title=""
            >
              
            </button>
          </div>
        )}

        {answer && (
          <div className="answer-container">
            <div className="answer-header">
              <h4> AI</h4>
              <div className="answer-actions">
                <button
                  type="button"
                  className="copy-button"
                  onClick={() => copyToClipboard(answer)}
                  title=""
                >
                   
                </button>
                <button
                  type="button"
                  className="clear-button"
                  onClick={handleClear}
                  title=""
                >
                  
                </button>
              </div>
            </div>
            
            {/*  */}
            {isStreaming && (
              <div className="streaming-progress">
                <div className="progress-bar">
                  <div className="progress-fill"></div>
                </div>
                <div className="progress-info">
                  <span className="progress-text">AI...</span>
                  <span className="char-count"> {answer.length} </span>
                </div>
              </div>
            )}
            
            <div className="answer-content">
              <div className="answer-text">
                {renderAnswer(answer)}
              </div>
            </div>
          </div>
        )}

        {!answer && !error && !isLoading && (
          <div className="help-container">
            <h4> </h4>
            <div className="help-content">
              <p></p>
              <ul>
                <li><em></em></li>
                <li><em></em></li>
                <li><em></em></li>
                <li><em></em></li>
                <li><em></em></li>
              </ul>
              <p><strong></strong>AI</p>
            </div>
          </div>
        )}
      </div>
    </>
  );
};

export default CodeSearch;

