export interface CallRelation {
  callerId: string;
  callerName: string;
  calleeId: string;
  calleeName: string;
  callerFileId: string;
  calleeFileId: string;
  callerScope: string;
  calleeScope: string;
  calleeEntityId?: string;
  callerEntityId?: string;
}

export enum RepoType {
  Local = 0,
  Github = 1
}

export enum Language {
  Golang = 0,
  Java = 1,
  Python = 2,
  Rust = 3
}

export enum FunScope {
  Default = 0,
  Struct = 1,
  Interface = 2,
  Constant = 3,
  Variable = 4
}

export interface Repo {
  id: string;
  name: string;
  repoType: RepoType;
  path?: string;
  target?: string;
  description?: string;
  excludes?: string;
  language: Language;
}

export interface CreateRepoReq {
  name: string;
  repoType: RepoType;
  path?: string;
  target?: string;
  token?: string;
  description?: string;
  excludes?: string;
  language: Language;
}

export interface ListReposResp {
  ret: BaseResp;
  body: {
    repo: Repo[];
  };
}

export interface GetRepoResp {
  ret: BaseResp;
  body: {
    repo: Repo;
  };
}

export interface PackageNode {
  id: string;
  name: string;
  parentId?: string;
}

export interface FileNode {
  id: string;
  name: string;
  pkgId: string;
}

export interface RepoTreeResp {
  ret: BaseResp;
  body: {
    packages: PackageNode[];
    files: FileNode[];
  };
}

export interface ApiResponse {
  ret: BaseResp;
  body?: any;
}

export interface Node {
  id: string;
  name: string;
  type: 'caller' | 'callee';
  level: number;
  expanded: boolean;
  children: Set<string>;
  parents: Set<string>;
  fileId?: string; // ID
  scope?: string; // 
  entityId?: string; // ID
  implementationChainId?: string; // 
}

export interface NodePosition {
  x: number;
  y: number;
}

export interface GraphState {
  nodes: Map<string, Node>;
  visibleNodes: Set<string>;
  nodePositions: Map<string, NodePosition>;
  allRelationships: CallRelation[];
}

// 
export interface SearchCodeReq {
  repoId: string;
  query: string;
  limit?: number;
  similarity_threshold?: number;
}

export interface SearchResult {
  id: string;
  path: string;
  content: string;
  document: string;
  logic: string;
  scope: string;
  similarity_score: number;
  highlights: string[];
}

export interface SearchCodeResp {
  code: number;
  msg: string;
  results: SearchResult[];
}

// Answer
export interface AnswerReq {
  id: string; // ID
  question: string;
}

export interface AnswerResp {
  answer: string;
  is_streaming?: boolean;  // 
  is_complete?: boolean;   // 
  chunk?: string;          // 
  chunk_index?: number;    // 
  error?: string;          // 
}



// 
export interface BaseResp {
  code: number;
  reason: string;
  msg: string;
}

// 
export interface CallChainResp {
  ret: BaseResp;
  body: {
    callRelations: CallRelation[];
  };
}

// 
export interface CreateRepoResp {
  ret: BaseResp;
  body: {
    id: string;
  };
}

// 
export interface DeleteRepoReq {
  id: string;
}

export interface DeleteRepoResp {
  ret: BaseResp;
}

// 
export interface AnalyzeRepoReq {
  id: string;
  forceUpdate: boolean;
}

export interface AnalyzeResp {
  ret: BaseResp;
}

// 
export interface GetRepoTreeReq {
  id: string;
}

export interface GetRepoTreeResp {
  ret: BaseResp;
  body: {
    packages: PackageNode[];
    files: FileNode[];
  };
}

// 
export interface CallChainReq {
  id: string;
}

// 
export interface ViewFileReq {
  repoId: string;
  id: string;
}

export interface ViewFileResp {
  ret: BaseResp;
  body: {
    Content: string;
    language: Language;
    functions: Function[];
  };
}

// 
export interface GetImplementReq {
  id: string;
}

export interface GetImplementResp {
  ret: BaseResp;
  body: {
    entities: Entity[];
  };
}

// 
export interface Entity {
  name: string;
  fileId: string;
  id: string;
  functions: Function[];
}

// 
export interface Function {
  id: string;
  fileId: string;
  name: string;
  receiver: string;
}

// 
export interface CallRelationship {
  callerId: string;
  callerName: string;
  calleeId: string;
  calleeName: string;
  calleeFileId: string;
  callerFileId: string;
  calleeScope: number;
  callerScope: number;
  calleeEntityId: string;
  callerEntityId: string;
}
